.Home {
    margin-bottom: 2cm;
    display: flex;
    flex-direction: column;
    row-gap: 3cm;
}

.para-box {
    display: flex;
    margin-top: 1cm;
    flex-direction: column;

    row-gap: 2cm;
}

.para-one {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.tagline {
    font-size: 40px;
    line-height: 1.05cm;
    font-weight: bolder;
    font-family: play;
    color: rgb(31, 76, 211);
    margin-bottom: .2cm;
    letter-spacing: .035cm;
}

.check {
    width: .52cm;
}

.nismT {
    margin-top: .2cm;
}

.explore {
    margin-top: .5cm;
    padding: .35cm .8cm;
    font-size: 18px;
    border: 3px solid royalblue;
    background-color: royalblue;
    color: white;
    border-radius: 1000px;
    cursor: pointer;
    transition: .4s;

    &:hover {
        background-color: transparent;
        color: black;
    }
}

.get-list-box {
    margin-top: .1cm;
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: .08cm;
}

.get-list {
    display: flex;
    align-items: center;
    column-gap: .08cm;
}

.projects-display-heading {
    font-size: 35px;
    text-align: center;
    font-family: Dancing Script;
    color: black;
    font-weight: 600;

}

.desc {
    font-size: 20px;
    font-family: sarabun;
    color: rgb(72, 72, 72);
}

.sub-tagline {
    font-size: 21px;
    font-family: sarabun;
    color: rgb(72, 72, 72);
}

.left-one {
    width: 18cm;
}

.corss {
    width: 11cm;
}

.course {
    width: 8.5cm;
    height: auto;
    border: 1px solid black;
    cursor: pointer;
}

.courses-container {
    display: flex;
    justify-content: center;
    column-gap: .5cm;
    flex-wrap: wrap;
    row-gap: .5cm;

}

.course-pic {
    padding: 0.2cm .2cm 0cm 0.2cm;
}

.thumbnail {
    width: 100%;
    height: auto;
}

.course-dets-section {
    padding: 0cm .2cm .2cm .2cm;
    display: flex;
    flex-direction: column;
    row-gap: .1.5cm;

}

.course-price-section {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
}

.course-name {
    font-family: play;
    font-size: 24px;
    font-weight: 600;
}

.course-price {
    font-family: play;
    font-size: 19px;
    font-weight: 600;
}

.course-owner {
    color: rgb(74, 74, 74);
    font-size: 14px;
}